

import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder, FormGroup, ValidatorFn, AbstractControl } from '@angular/forms';
import { MasterService } from 'app/shared/services/master/master.service';
import { VehicleService } from 'app/shared/services/master/vehicle/vehicle.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ClientService } from 'app/shared/services/client/client.service';
import { CpoService } from 'app/shared/services/cpo/cpo.service';
import { ChargingStationService } from 'app/shared/services/charging-station/charging-station.service';
import { DateService } from 'app/shared/services/utility/date.service';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { DatePipe } from '@angular/common';
import { MapDialogComponent } from 'app/shared/components/map-dialog/map-dialog.component';
import { GeoLocationService } from 'app/shared/services/map/geo-location.service'
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CountriesService } from 'app/shared/services/countries.service';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { ChargerService } from 'app/shared/services/charger/charger.service';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { ChargerLogSharedDialogComponent } from 'app/shared/components/charger-log-shared-dialog/charger-log-shared-dialog.component';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';

function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}
@Component({
  selector: 'app-view-details',
  templateUrl: './view-details.component.html',
  styleUrls: ['./view-details.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations,
  providers: [DatePipe, GeoLocationService]
})
export class ViewDetailsComponent implements OnInit {
  chargingStation: any;
  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
  chargers: any;
  localStorage: any;
  currentlyOpenedItemIndex = -1;
  previousTabIndex: any;
  // messageConstant = MessageConstant;
  constructor(public dialog: MatDialog, private fb: FormBuilder, private cpoService: CpoService,
    private chargingStationService: ChargingStationService, private snackBar: MatSnackBar, private chargerService: ChargerService,
    private router: Router, private dateService: DateService, private datePipe: DatePipe, public geoLocationService: GeoLocationService, public countryService: CountriesService) {
  }

  ngOnInit() {
    this.chargingStation = this.chargingStationService.chargerDetail;
    this.previousTabIndex = this.chargingStationService.tabIndex;
    //this.chargers = this.chargingStationService.chargerDetail.chargers;
    this.initialize();
  }

  initialize() {

    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    if (this.chargingStation == null || this.chargingStation == undefined) {
      this.router.navigate(['/login']);
    }
    this.getChargers(this.chargingStation.id);
  }
  getChargers(station_id) {
    this.chargerService.getChargersByMappedStationId(station_id).subscribe(
      (chargerData: any) => {
        console.log("chargerData");
        console.log(chargerData);
        
        if (chargerData.status == true) {
          this.chargers = chargerData.data;
        }

      }, (err) => {

      }
    )
  }
  setOpened(itemIndex) {
    this.currentlyOpenedItemIndex = itemIndex;
  }

  setClosed(itemIndex) {
    if (this.currentlyOpenedItemIndex === itemIndex) {
      this.currentlyOpenedItemIndex = -1;
    }
  }
  openDialogChargerDetails(data) {
    event.stopPropagation();
    let params = {
      data: data
    }
    const dialogRef = this.dialog.open(ChargerLogSharedDialogComponent, {
      width: '1800px',
      height: '900px',
      data: params,
      autoFocus: false,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {

      //this.getChargersByMappedStationId(this.stationDetails.id);
    });
  }
  manageChargerLog(data) {


    //localStorage.setItem('station', JSON.stringify( data));
    this.chargerService.chargerDetail = data;
    // const url = this.router.serializeUrl(
    //   this.router.createUrlTree(['/logsandHistory/viewChargerLog'])
    // );
    this.router.navigate([]).then(result => { window.open(`/#/logsandHistory/viewChargerLog/${data.name}`, '_blank'); });
    // window.open(url, '_blank');
    //  this.router.navigate([]).then(result => {  window.open(url, '_blank'); });
    //this.router.navigate(['/logsandHistory/viewChargerLog']);
  }
  backButton() {
    if (this.previousTabIndex != null || this.previousTabIndex != undefined) {
      // this.previousTabIndex = null;
      this.router.navigate(['/home', this.previousTabIndex]);

    } else {
      this.router.navigate(['/chargingStation/crud/list']);
    }

  }


  removeChargerFromStation(data) {
    this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
      disableClose: true
    });

    //this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete?';

    this.confirmDialogRef.afterClosed().subscribe(result => {

      if (result) {
        let params: any = {

          id: data.map_id,
          modify_by: this.localStorage.userId
        }
        this.chargerService.removeChargerFromStation(params).subscribe(

          (resp: any) => {
            //
            if (resp.status == true) {
              this.snackBar.open(MessageConstant.ChargerRemoved, 'Dismiss', { duration: 5000 });
              this.getChargers(this.chargingStation.id);
              // this.router.navigate(['/chargingStation/manageChargers/list']);
              // this.getChargersByMappedStationId(this.stationDetails.id);
            } else {
              this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
            }
          },
          (err) => {
            ////
            this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
          }
        )

      }
      this.confirmDialogRef = null;
    });

  }
}

