import { Component, OnInit, ViewChild, Inject } from "@angular/core";
import {
    Validators,
    FormBuilder,
    FormGroup,
    ValidatorFn,
    AbstractControl,
} from "@angular/forms";
import { MatSnackBar } from "@angular/material/snack-bar";
import { Router, ActivatedRoute } from "@angular/router";
import { ChargerService } from "app/shared/services/charger/charger.service";
import { ChargingStationService } from "app/shared/services/charging-station/charging-station.service";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { MatDialog } from "@angular/material/dialog";
import { MessageConstant } from "app/shared/utils/message.constant";
import { ViewEncapsulation } from "@angular/core";
import { fuseAnimations } from "@fuse/animations";
import { MatAutocompleteTrigger } from "@angular/material/autocomplete";
import { Observable } from "rxjs";
import { map, startWith } from "rxjs/operators";
import { MtxDialogData } from "@ng-matero/extensions";
function autocompleteObjectValidator(): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } | null => {
        if (typeof control.value === "string") {
            return { invalidAutocompleteObject: { value: control.value } };
        }
        return null; /* valid option selected */
    };
}
export class Charger {
    id: string;
    name: string;
    serial_no: string;
    station_id: number;
}

@Component({
    selector: "app-multi-charger-dialog",
    templateUrl: "./multi-charger-dialog.component.html",
    styleUrls: ["./multi-charger-dialog.component.scss"],
    encapsulation: ViewEncapsulation.None,
    animations: fuseAnimations,
})
export class MultiChargerDialogComponent implements OnInit {
    params: any;
    form: FormGroup;
    stations: any;
    localStorage: any;
    status = false;
    stationDetails: any;
    isEditMode = false;
    client_id: any;
    @ViewChild("toggleAvailable") toggleAvailable: any;
    @ViewChild("toggleElement") ref: any;
    selectedChargerSerial = [];
    public chipSelectedSerialNo: Charger[] = [];
    chargerSerials: any;
    chargerSerialsOrg: any;
    selectedSerialNo = [];
    unitType: any;
    removable = true;
    isAvailable = false;
    public filteredSerial: Observable<String[]>;
    filteredOptionSerial: Observable<any[]>;
    // messageConstant = MessageConstant;
    constructor(
        private fb: FormBuilder,
        private chargerService: ChargerService,
        private snackBar: MatSnackBar,
        public dialogRef: MatDialogRef<MultiChargerDialogComponent>,
        private router: Router,
        public dialog: MatDialog,
        @Inject(MAT_DIALOG_DATA) public data: MtxDialogData
    ) {}

    ngOnInit() {
        this.initialize();
    }

    initialize() {
        this.localStorage = JSON.parse(localStorage.getItem("userDetails"));

        this.stationDetails = JSON.parse(localStorage.getItem("station"));

        this.form = this.fb.group({
            serialNumber: [""],
            name: [""],
            station: [{ value: "", disabled: true }],
            cpo_name: [{ value: "", disabled: true }],
            noOfGuns: [{ value: "", disabled: true }],
            addressLine1: [{ value: "", disabled: true }],
            addressLine2: [{ value: "", disabled: true }],
            city: [{ value: "", disabled: true }],
            state: [{ value: "", disabled: true }],
            country: [{ value: "", disabled: true }],
            status: this.status == true ? "Y" : "N",
        });
        this.params = this.data;
        this.client_id = this.params.data.client_id;
        this.getMultipleChargerSerial();

        this.assignValues(this.params);
    }

    assignValues(params) {
        this.form.get("station").setValue(params.data.name);
        this.form.get("cpo_name").setValue(params.data.cpo_name);
        this.form.get("noOfGuns").setValue(params.data.no_of_guns);
        this.form.get("addressLine1").setValue(params.data.address1);
        this.form.get("addressLine2").setValue(params.data.address2);
        this.form.get("city").setValue(params.data.city_name);
        this.form.get("state").setValue(params.data.state_name);
        this.form.get("country").setValue(params.data.country_name);
    }

    onSubmit() {
        if (this.form.valid) {
            let formValues = this.form.value;

            let params: any = {
                station_id: this.stationDetails.id,
                charger_data: this.selectedSerialNo.map((v) => ({
                    charger_id: v.charger_id,
                    serial_no: v.serial_no,
                })),

                status: this.status == true ? "Y" : "N",
            };

            if (this.isEditMode) {
                (params.id = this.stationDetails.id),
                    (params.modify_by = this.localStorage.userId);
            } else {
                params.created_by = this.localStorage.userId;
            }

            if (this.isEditMode) {
            } else {
                this.chargerService
                    .addChargerToStationMultiple(params)
                    .subscribe(
                        (data: any) => {
                            //
                            if (data.status == true) {
                                this.snackBar.open(
                                    MessageConstant.chargerMappedWithStation,
                                    "Dismiss",
                                    { duration: 5000 }
                                );
                                this.dialogRef.close();
                                this.router.navigate([
                                    "/chargingStation/crud/list",
                                ]);
                            } else {
                                this.snackBar.open(
                                    MessageConstant.errorMessage,
                                    "Dismiss",
                                    { duration: 5000 }
                                );
                                this.dialogRef.close();
                            }
                        },
                        (err) => {
                            ////
                            this.snackBar.open(
                                MessageConstant.errorMessage,
                                "Dismiss",
                                { duration: 5000 }
                            );
                        }
                    );
            }
        } else {
            return;
        }
    }

    getErrorMessage(form: FormGroup) {
        return form.get("email").hasError("required")
            ? "You must enter a value"
            : form.get("email").hasError("email")
            ? "Not a valid email"
            : "";
    }

    close() {
        this.dialogRef.close();
    }

    onYesClick() {
        this.onSubmit();
    }

    getMultipleChargerSerial() {
        this.chargerService
            .getClientChargersNotMappedToAnyStation(this.client_id)
            .subscribe(
                (multiChargerData: any) => {
                    //this.form.reset();
                    if (multiChargerData.status == true) {
                        this.chargerSerials = multiChargerData.data;
                        this.chargerSerialsOrg = [...this.chargerSerials];
                        this.filteredOptionSerial =
                            this.form.controls.serialNumber.valueChanges.pipe(
                                startWith(""),
                                map((value) =>
                                    typeof value === "string"
                                        ? value
                                        : value.serial_no
                                ),
                                map((serial_no) =>
                                    serial_no
                                        ? this._filterSerial(serial_no)
                                        : this.chargerSerials
                                )
                            );
                    } else {
                        this.snackBar.open(
                            "All charger are mapped with other station",
                            "Dismiss",
                            { duration: 5000 }
                        );
                    }
                },
                (err) => {}
            );
        // this.chargerSerials =  this.chargingStationService.getMultipleChargerSerial();
        // this.chargerSerialsOrg = [...this.chargerSerials];
    }
    displaySerial(item: any): string {
        return item && item.serial_no ? item.serial_no : "";
    }
    private _filterSerial(name: string): any[] {
        const filterValue = name.toLowerCase();
        this.chargerSerials = this.resetChrgerList();

        return this.chargerSerials.filter((option) =>
            option.serial_no.toLowerCase().includes(filterValue)
        );
    }
    resetChrgerList(): any[] {
        var chargerSerials = [];
        if (
            this.selectedSerialNo != undefined &&
            this.selectedSerialNo.length > 0
        ) {
            var isExist = true;

            for (var k = 0; k < this.chargerSerialsOrg.length; k++) {
                var serialNo = this.chargerSerialsOrg[k];
                isExist = false;
                for (var i = 0; i < this.selectedSerialNo.length; i++) {
                    var selectedSerialNo = this.selectedSerialNo[i];
                    if (selectedSerialNo.serial_no == serialNo.serial_no) {
                        isExist = true;
                    }
                }

                if (!isExist) {
                    chargerSerials.push(serialNo);
                }
            }
        } else {
            chargerSerials = [...this.chargerSerialsOrg];
        }
        return chargerSerials;
    }
    onSerialSelected(event) {
        this.selectedSerialNo.push(event.option.value);
        //this.form.controls['serialNumber'].setValue('');
        this.form.get("serialNumber").setValue("");
        this.chargerSerials = this.resetChrgerList();
        this.unitType += 1;
        this.filteredOptionSerial =
            this.form.controls.serialNumber.valueChanges.pipe(
                startWith(""),
                map((value) =>
                    typeof value === "string" ? value : value.serial_no
                ),
                map((serial_no) =>
                    serial_no
                        ? this._filterSerial(serial_no)
                        : this.chargerSerials
                )
            );
    }
    public removeSerial(i): void {
        this.selectedSerialNo.splice(i, 1);
        this.chargerSerials = this.resetChrgerList();
        this.unitType - 1;
        this.filteredOptionSerial =
            this.form.controls.serialNumber.valueChanges.pipe(
                startWith(""),
                map((value) =>
                    typeof value === "string" ? value : value.serial_no
                ),
                map((serial_no) =>
                    serial_no
                        ? this._filterSerial(serial_no)
                        : this.chargerSerials
                )
            );
    }
    selectionMade(event: Event, trigger: MatAutocompleteTrigger) {
        event.stopPropagation();
        trigger.openPanel();
    }
    updateAvailable(event) {
        this.status = event.checked;
        this.ref.checked = this.status;
    }
    closeActionPanel() {
        this.dialogRef.close();
    }
}
