

import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActionDialogComponent } from '../../action-dialog/action-dialog.component';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ChargingStationService } from 'app/shared/services/charging-station/charging-station.service';
import { AddChargerDialogComponent } from '../../add-charger-dialog/add-charger-dialog.component';
import { ActivatedRoute, Router } from '@angular/router';
import { SelectionModel } from '@angular/cdk/collections';
import { ChargerService } from 'app/shared/services/charger/charger.service';
import { ChargerLogSharedDialogComponent } from 'app/shared/components/charger-log-shared-dialog/charger-log-shared-dialog.component';
import { MapChargerDialogComponent } from '../map-charger-dialog/map-charger-dialog.component';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { MatTableFilter } from 'mat-table-filter';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
export class Charger {
  id: string;
  name: string;
  serial_no: string;
  batch_id: number;
  station_id: number;
  current_version_id: number;
  no_of_guns: number;
  Address: string;
  Lat: number;
  Lng: number;
  OTA_Config: string;
  Periodic_Check_Ref_Time: string;
  Periodicity_in_hours: number;
  When_to_Upgrade: string;
  Upgrade_Specific_Time: number;
  is_available: number;
  status: string;
  created_date: string;
  createdby: number;
  modify_date: string;
  modifyby: number;
  statusString: string;
  availableString: string;
}

@Component({
  selector: 'app-list-chargers',
  templateUrl: './list-chargers.component.html',
  styleUrls: ['./list-chargers.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations
})
export class ListChargersComponent implements OnInit {
  filterEntity: Charger;
  filterType: MatTableFilter;
  displayedColumns: string[] = [
    'serial_no',
    'name',
    'station_name',
    'version_name',
    'no_of_guns',
    'Address',
    'Lat',
    'Lng',
    'is_available',
    'status',
    'action'
  ];
  dataSource: MatTableDataSource<Charger>;

  chargers: Charger[];
  stationDetails: any;
  localStorage: any;

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  selection = new SelectionModel<Charger>(true, []);
  station: any;
  //messageConstant = MessageConstant;
  constructor(private chargerService: ChargerService, public dialog: MatDialog,
    private snackBar: MatSnackBar, private router: Router, private route: ActivatedRoute) { }

  ngOnInit() {

    
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.station = JSON.parse(this.route.snapshot.paramMap.get('station'));
    this.stationDetails = JSON.parse(localStorage.getItem('station'));


    this.getChargersByMappedStationId(this.stationDetails.id);
    this.filterEntity = new Charger();
    this.filterType = MatTableFilter.ANYWHERE;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }


  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {

    const numSelected = this.selection.selected.length;
    const numRows = !!this.dataSource ? this.dataSource.data.length : 0;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.dataSource.data.forEach(row => this.selection.select(row));
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: Charger): string {
    if (!row) {
      return `${this.isAllSelected() ? 'select' : 'deselect'} all`;
    }
    return `${this.selection.isSelected(row) ? 'deselect' : 'select'} row ${row.id + 1}`;
  }

  getChargersByMappedStationId(stationId) {
    //
    this.chargerService.getChargersByMappedStationId(stationId).subscribe(
      (data: any) => {
        
        if (data != null) {
          this.chargers = data.data;
          for (var i = 0; i < this.chargers.length; i++) {
            if (this.chargers[i].status == 'N') {
              this.chargers[i].statusString = 'Inactive';
            } else {
              this.chargers[i].statusString = 'Active';
            }
            if (this.chargers[i].is_available == 0) {
              this.chargers[i].availableString = 'No';
            } else {
              this.chargers[i].availableString = 'yes';
            }


          }
          this.dataSource = new MatTableDataSource(this.chargers);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        } else {
          return;
        }

      }, (err) => {

      }
    )
  }

  delete(id) {

    this.chargerService.delete(id).subscribe(
      (data: any) => {

        this.getChargersByMappedStationId(this.stationDetails.id);
        this.snackBar.open(MessageConstant.deleteMessage, 'Dismiss', { duration: 2000 });
      }, (err) => {

      }
    )
  }

  removeChargerFromStation(data) {

    let params: any = {
      id: data.map_id,
      charger_id: data.charger_id,
      station_id: this.stationDetails.id,
      modify_by: this.localStorage.userId
    }



    // if(this.isEditMode){

    // }else{
    //   
    //;
    this.chargerService.removeChargerFromStation(params).subscribe(
      (data: any) => {
        //
        this.snackBar.open(MessageConstant.ChargerRemoved, 'Dismiss', { duration: 5000 });
        // this.router.navigate(['/chargingStation/manageChargers/list']);
        this.getChargersByMappedStationId(this.stationDetails.id);
      },
      (err) => {
        ////
        this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
      }
    )

    // }


  }


  openDialog(action, data): void {

    let params = {
      action: action,
      data: data
    }

    const dialogRef = this.dialog.open(MapChargerDialogComponent, {
      width: '1200px',

      data: params
    });

    dialogRef.afterClosed().subscribe(result => {

      // this.moderate(result);
      this.getChargersByMappedStationId(this.stationDetails.id);
    });
  }

  addChargerDialog(data) {


    let params = {
      data: data
    }

    const dialogRef = this.dialog.open(AddChargerDialogComponent, {
      width: '1200px',
      data: params,
      autoFocus: false,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      // this.moderate(result);
      this.getChargersByMappedStationId(this.stationDetails.id);
    });
  }

  manageCharger(data) {

    this.router.navigate(['/chargingStation/manageChargers', { station: data }]);
  }

  openDialogChargerDetails(data) {


    let params = {
      data: data
    }

    const dialogRef = this.dialog.open(ChargerLogSharedDialogComponent, {
      width: '1600px',
      height: '500px',
      data: params,
      autoFocus: false,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {

      //this.getChargersByMappedStationId(this.stationDetails.id);
    });
  }
}




