import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ChargingStationCrudComponent } from './charging-station-crud/charging-station-crud.component';
import { TABS_DEMO_ROUTES } from './charging-station-crud/routes';
import { ManageChargersComponent } from './manage-chargers/manage-chargers.component';
import { TABS_MANAGE_CHARGER_ROUTES } from './manage-chargers/routes';
import { ViewDetailsComponent } from './view-details/view-details.component';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes: Routes = [
    // { path : 'create', component : CreateComponent},
    // { path : 'edit/:id', component : EditComponent},
    // { path : 'list', component : ListComponent},
    // { path : '', redirectTo : 'list',pathMatch:'full'},
    { path: 'crud', component: ChargingStationCrudComponent, canActivate: [AuthGuard], children: TABS_DEMO_ROUTES },
    { path: 'manageChargers', component: ManageChargersComponent, children: TABS_MANAGE_CHARGER_ROUTES },
    { path: 'viewDetails', component: ViewDetailsComponent },
    { path: '', redirectTo: 'crud', pathMatch: 'full' }
]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})

export class ChargingStationRoutingModule { }