import { Routes } from '@angular/router';
import { ListChargersComponent } from './list-chargers/list-chargers.component';

import { MapChargerComponent } from './map-charger/map-charger.component';
import { MapChargerDialogComponent } from './map-charger-dialog/map-charger-dialog.component';
export const TABS_MANAGE_CHARGER_ROUTES: Routes = [
  { path: 'map', component: MapChargerComponent },
  { path: 'list', component: ListChargersComponent },
  { path: 'map-charger-dialog', component: MapChargerDialogComponent },
  { path: '', redirectTo: 'list', pathMatch: 'full' },
];
