import { Component, OnInit, ViewChild } from '@angular/core';
import { Validators, FormBuilder, FormGroup,ValidatorFn,AbstractControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router,ActivatedRoute } from '@angular/router';
import { ChargerService } from 'app/shared/services/charger/charger.service';
import { ChargerBatchService } from 'app/shared/services/charger-batch/charger-batch.service';
import { ChargingStationService } from 'app/shared/services/charging-station/charging-station.service';
import { VersionService } from 'app/shared/services/version/version.service';
import {CountriesService} from 'app/shared/services/countries.service';
import{GeoLocationService} from 'app/shared/services/map/geo-location.service';
import { MapDialogComponent } from 'app/shared/components/map-dialog/map-dialog.component';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import {MessageConstant} from 'app/shared/utils/message.constant';
import { MatDialog } from '@angular/material/dialog';
function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}
@Component({
  selector: 'app-map-charger',
  templateUrl: './map-charger.component.html',
  styleUrls: ['./map-charger.component.scss'],
  providers:[GeoLocationService]
})
export class MapChargerComponent implements OnInit {
 

  form: FormGroup;
  batches:any;
  stations:any;
  versions :any;
  isAvailable = false;
  localStorage : any;
  status = false;
  stationDetails : any;
  isEditMode = false; 
  mapDetails : any;
  
  mLat: any;
  mLong:any;
  filteredOptionCountry: Observable<any[]>;
  filteredOptionState: Observable<any[]>;
  filteredOptionCity: Observable<any[]>;
  stateName:any;
  cities:any;
  countries:any;
  states:any;
  pin_code_id:any;
  country_id:any;
  state_id:any;
  @ViewChild("toggleAvailable") toggleAvailable: any;
  constructor(private fb: FormBuilder, 
    private chargerBatchService : ChargerBatchService, private chargingStationService : ChargingStationService, 
    private versionService : VersionService, private chargerService : ChargerService,
    private snackBar: MatSnackBar,public dialog : MatDialog,private router : Router,
    public geoLocationService:GeoLocationService,public countryService: CountriesService) {  }

  ngOnInit() {
    this.initialize();
  }

  initialize(){

    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));
    
    this.stationDetails = JSON.parse( localStorage.getItem('station'));
    
    // this.stationDetails = this.route.snapshot.paramMap.get('station');

    this.form = this.fb.group({
      name: ['', ],
      serialNo: ['', [Validators.required]],
      station: [{value : '',disabled:true}, ],
      noOfGuns: ['', [Validators.required]],
      addressLine1:[''],
      addressLine2:[''],
      landmark:[''],
      city:[''],
      state:[''],
      country:[''],
      pincode:[''],
      lat: ['', ],
      long: ['', ],
      isAvailable: ['', [Validators.required]],
    });
   
    this.getBatches();
    this.getStations();
    this.getVersions();
     //this.getCountry();
    // this.getAllCountry();
    // this.getAllState();
     //this.getAllCity();
  }



  getBatches(){
    this.chargerBatchService.getChargerBatches().subscribe(
      (data:any)=>{
        
        this.batches = data;
      },(err)=>{
      }
    )
  }

  getStations(){
    
    this.chargingStationService.getChargingStations().subscribe(
      (data:any)=>{
        // 
        this.stations = data.data;
       
      },(err)=>{
        
      }
    )
  }

  getVersions(){
    this.versionService.getVersions().subscribe(
      (data:any)=>{
        
        this.versions = data;
      },(err)=>{
        
      }
    )
  }

  getChargerBySerialNo(){
    let params = {
      srNo : this.form.value.serialNo.trim(),
      station_id : this.stationDetails.id
    }
    // let srNo = this.form.value.serialNo.trim();
//
    this.chargerService.getChargerBySerialNo(params).subscribe(
      (data:any)=>{
        //
        this.form.reset();
        if(data.length>0){
          this.mapDetails = data[0];
          
          if(this.mapDetails.map_id > 0 && this.mapDetails.map_status != 'D' ){
            this.isEditMode=true;
            this.snackBar.open(MessageConstant.chargerAlMapped, 'Dismiss', { duration: 3000 });

          }else{
            this.isEditMode=false;
            this.assignValues(this.mapDetails);
            this.getAllCountry();
            this.getAllState();
            this.getAllCity();
          }
        }
      },(err)=>{
        
      }
    )
  }

  assignValues(params){
    this.form.get('name').setValue(params.name);
    this.form.get('serialNo').setValue(params.serial_no); 
    this.form.get('noOfGuns').setValue(params.no_of_guns); 
  
    this.form.get('lat').setValue(this.stationDetails.lat); 
    this.form.get('long').setValue(this.stationDetails.lng); 
    this.form.get('addressLine1').setValue(this.stationDetails.address1); 
    this.form.get('addressLine2').setValue(this.stationDetails.address2); 
    this.form.get('pincode').setValue(this.stationDetails.PIN);
    this.form.get('landmark').setValue(this.stationDetails.landmark);
    this.form.get('city').setValue(this.stationDetails.city_name);
    this.form.get('state').setValue(this.stationDetails.state_name);
    this.form.get('country').setValue(this.stationDetails.country_name);
    const toSelect = this.stations.find(c => c.id == this.stationDetails.id);
    this.form.get('station').setValue(toSelect.id);
  
    this.status = params.status == 'Y' ? true : false;
    this.isAvailable = params.is_available == 1 ? true : false;
  }


  onSubmit(){
    let formValues = this.form.value;
    
    let params:any = {
      charger_id : this.mapDetails.id , 
      station_id : this.stationDetails.id , 
      name :formValues.name, 
      address1 :formValues.addressLine1,
      address2 :formValues.addressLine2,
      landmark:formValues.landmark,
      country:formValues.country.id,
      state:formValues.state.id,
      city:formValues.city.id,
      PIN :formValues.pincode ,
      lat :formValues.lat, 
      lang :formValues.long,
      is_available :this.isAvailable, 
      status :'Y' ,
    }

    if (this.isEditMode){
      params.id = this.stationDetails.id,
      params.modify_by = this.localStorage.userId;
    } else{
      params.created_by = this.localStorage.userId;
    }
    
    

    if(this.isEditMode){

    }else{
      
      this.chargerService.addChargerToStation(params).subscribe(
        (data:any )=>{
          //
          this.snackBar.open(MessageConstant.chargerNCreate, 'Dismiss', { duration: 5000 });
          this.router.navigate(['/chargingStation/crud/list']);
        },
        (err)=>{
          ////
          this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
        }
      )

    }

  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
      ? 'Not a valid email'
      : '';
  }

  updateAvailable(event){
    this.isAvailable = event.checked;
    this.toggleAvailable.checked = this.isAvailable;
  }
  getAllCountry(){
    this.countryService.getAllCountry().subscribe(
      (data:any)=>{
        //;
        this.countries = data;
        if(!this.isEditMode){
          //;
          const toSelect = this.countries.find(c => c.id == this.stationDetails.country_id);
          this.form.get('country').setValue(toSelect);
         
          this.countries = this.form.controls.country.valueChanges
           .pipe(
          startWith(''),
          //tap(() => this.form.controls.chargingModel.setValue(toSelect)),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterCountry(name) : this.countries)
        );
        }
       
         },(err)=>{
           
         }
    )
  }
  getAllState(){
    this.countryService.getAllState().subscribe(
      (data:any)=>{
        //;
        this.states = data;
        if(!this.isEditMode){
          //;
          const toSelect = this.states.find(c => c.id == this.stationDetails.state_id);
          this.form.get('state').setValue(toSelect);
         
          this.states = this.form.controls.state.valueChanges
           .pipe(
          startWith(''),
          //tap(() => this.form.controls.chargingModel.setValue(toSelect)),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterState(name) : this.states)
        );
        }
       
         },(err)=>{
           
         }
    )
  }
  
  getAllCity(){
    this.countryService.getAllCities().subscribe(
      (data:any)=>{
        //;
        this.cities = data;
        if(!this.isEditMode){
          //;
          const toSelect = this.cities.find(c => c.id == this.stationDetails.city_id);
          this.form.get('city').setValue(toSelect);
         
          this.cities = this.form.controls.city.valueChanges
           .pipe(
          startWith(''),
          //tap(() => this.form.controls.chargingModel.setValue(toSelect)),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterCity(name) : this.cities)
        );
        }
       
         },(err)=>{
           
         }
    )
  }
  getStateCountry(){
    
    this.pin_code_id = this.form.value.pincode;
  
  //;
  this.countryService.getCountryStateByPIN(this.pin_code_id ).subscribe(
    (data:any)=>{
      //this.form.reset();
      if(data != undefined && data != null && data.length > 0){
        this.state_id = data[0].state_id;
        //;
        this.countryService.getCountryByState(this.state_id ).subscribe(
          (countrydata:any)=>{
            //this.form.reset();
            this.countries = countrydata;
            const toSelect = this.countries.find(c => c.id == data[0].country_id);
            this.form.get('country').setValue(toSelect);
            
            this.filteredOptionCountry = this.form.controls.country.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterCountry(name) : this.countries)
            );
            //; 
            this.country_id = data[0].country_id;
            this.countryService.getStateByCountry(this.country_id ).subscribe(
              (statedata:any)=>{
                //this.form.reset();
                this.states = statedata;
                const toSelect = this.states.find(c => c.id == data[0].state_id);
                this.form.get('state').setValue(toSelect);
                
                this.filteredOptionState = this.form.controls.state.valueChanges
                .pipe(
                  startWith(''),
                  map(value => typeof value === 'string' ? value : value.name),
                  map(name => name ? this._filterState(name) : this.states)
                );
                
              },(err)=>{
                
              }
            )
            //; 
            this.state_id = data[0].state_id;
            this.countryService.getCityByState(this.state_id ).subscribe(
              (Citydata:any)=>{
                //this.form.reset();
                this.cities = Citydata;
                //const toSelect = this.states.find(c => c.id == data[0].state_id);
               // this.form.get('state').setValue(toSelect);
                
                this.filteredOptionCity = this.form.controls.city.valueChanges
                .pipe(
                  startWith(''),
                  map(value => typeof value === 'string' ? value : value.name),
                  map(name => name ? this._filterCity(name) : this.cities)
                );
                
              },(err)=>{
                
              }
            )
          },(err)=>{
            
          }
        )
      }else{

      }
     
      
    },(err)=>{
      
    }
  )
}
onCountryChange(event){
  this.countries = [];
  this.getAllCountry();
  this.getStatesByCountry(event);
  
 
  //this.form.get('pincode').setValue( "");
  this.form.get('state').setValue( "");
  this.form.get('city').setValue( "");
}
getStatesByCountry(state){
   this.state_id = state.id;
   this.countryService.getStateByCountry(this.state_id ).subscribe(
    (statedata:any)=>{
      //this.form.reset();
      this.states = statedata;
     // const toSelect = this.states.find(c => c.id == data[0].state_id);
     // this.form.get('state').setValue(toSelect);
      
      this.filteredOptionState = this.form.controls.state.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filterState(name) : this.states)
      );
      
    },(err)=>{
      
    }
  )
    
    
  
}
onStateChange(event){
  this.getCitiesByStates(event);
  this.getCountryByState(event);
  this.form.get('city').setValue( "");
}
getCitiesByStates(stateId){
  this.state_id = stateId.id;
   this.countryService.getCityByState(this.state_id ).subscribe(
    (citiesdata:any)=>{
      //this.form.reset();
      this.cities = citiesdata;
     // const toSelect = this.states.find(c => c.id == data[0].state_id);
     // this.form.get('state').setValue(toSelect);
      
      this.filteredOptionCity = this.form.controls.city.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filterCity(name) : this.cities)
      );
      
    },(err)=>{
      
    }
  )
 
}
getCountryByState(stateId){
  this.state_id = stateId.id;
  this.countryService.getCountryByState(this.state_id ).subscribe(
    (countrydata:any)=>{
      //this.form.reset();
      this.countries = countrydata;
      const toSelect = this.countries.find(c => c.id == countrydata[0].id);
      this.form.get('country').setValue(toSelect);
      
      this.filteredOptionCountry = this.form.controls.country.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filterCountry(name) : this.countries)
      );
     
   },(err)=>{
     
   }
 )
}
displayCountry(item: any): string {
  return item && item.name ? item.name : '';
}
private _filterCountry(name: string): any[] {
  const filterValue = name.toLowerCase();
  return this.countries.filter(option => option.name.toLowerCase().includes(filterValue));
}
displayState(item: any): string {
  return item && item.name ? item.name : '';
}
private _filterState(name: string): any[] {
  const filterValue = name.toLowerCase();
  return this.states.filter(option => option.name.toLowerCase().includes(filterValue));
}

displayCity(item: any): string {
  return item && item.name ? item.name : '';
}
private _filterCity(name: string): any[] {
  const filterValue = name.toLowerCase();
  return this.cities.filter(option => option.name.toLowerCase().includes(filterValue));
}
  getLatLong(){
    let params = {
    }
	

    const dialogRef = this.dialog.open(MapDialogComponent, {
      width: '700px',
      height:'410px',
      data: params
    });

    dialogRef.afterClosed().subscribe(result => {
      if(result != null || result != undefined){
        this.form.get('lat').setValue(result[0].lat);
        this.form.get('long').setValue(result[0].lng);
      }else{
        this.form.get('lat').setValue(this.mLat);
        this.form.get('long').setValue(this.mLong);
        this.snackBar.open(MessageConstant.customLocationMessage, 'Dismiss', { duration: 5000 });
      }
     
    });
  }
}



